<template>
  <div>
    <HeaderH class="glavni" title="Destinacije u Ponudi"/>
  <div class="telo">
    


    <div class="naslov2">
      <h2>Destinacije u ponudi: </h2>
    </div>
  
  
    <div class="dugmad">
      <button class="btn1" @click="prev()" :disabled="current === 0">Prethodna strana</button>
      
      <button class="btn1" @click="next()" :disabled="current >= 20 - 6">Sledeca strana</button>

      
    </div>

  
    <ListDestination :destinacije="sveDestinacije.slice(current, current + 6)"/>
  </div>
</div>
</template>

<script>
import HeaderH from '../components/HeaderH.vue';
import ListDestination from '../components/ListDestination.vue';

export default {
  name: 'App',
  components: {
    HeaderH,
    ListDestination
  },
  data() {
    return {
      headerTitle: "IBooking sajt",
      sveDestinacije: null,
      current: 0
    }
  },
  methods: {
    next() {
      if (this.current < this.sveDestinacije.length - 6) {
        this.current += 6;
      }
    },
    prev() {
      if (this.current >= 6) {
        this.current -= 6;
      }
    },
    getPathForDestination(destinationId) {
      const destination = this.sveDestinacije.find(dest => dest.id === destinationId);
      return destination ? destination.putanja : 'Destinacija nije pronađena';
    }
  },
  mounted() {
    fetch("http://localhost:9000/destinacije/")
      .then(res => res.json())
      .then(data => {
        this.sveDestinacije = data;
      });
  }
}
</script>

