<template>
    <div class="destinacija" @click="kliknutaDestinacija(destinacija.id)">
      <img :src="'/destinacije/' + destinacija.putanja" width="350" height="200">
      <h3>{{ destinacija.drzava }}</h3>
    </div>
  </template>
  
  <script>
  export default {
    name: 'SingleDestination',
    props: {
      destinacija: Object
    },
    methods:{
    kliknutaDestinacija(id){
        this.$router.push({ name: 'Destinacija', params: { id: id } });
      }
    }

  }
  </script>
  
  <style scoped>
  .destinacija {
    width: 33.33%;
    
    border:2px solid #fff;
    -moz-box-shadow: 10px 10px 5px #ccc;
    -webkit-box-shadow: 10px 10px 5px #ccc;
    box-shadow: 10px 10px 5px #ccc;
    -moz-border-radius:25px;
    -webkit-border-radius:25px;
    border-radius:25px;
  }
  </style>
  