<template>
    <div id="app"><div class="naslov">
      <HeaderH class="naslov" title="Log In"/>
      <b-form @submit="onSubmit">
        <b-form-group label="User Name:" label-for="name">
          <b-form-input id="name" v-model="form.name" placeholder="Enter name" required></b-form-input>
        </b-form-group>
        <b-form-group label="Password:" label-for="password">
          <b-form-input id="password" v-model="form.password" type="password" required></b-form-input>
        </b-form-group>
        <b-button type="submit" variant="primary">Login</b-button>
      </b-form>
    </div>
    </div>
  </template>
  
  <script>
import HeaderH from '@/components/HeaderH.vue';
import { mapActions } from 'vuex';

export default {
  name: 'Login',
  components: {
    HeaderH
  },
  data() {
    return {
      form: {
        name: '',
        password: ''
      }
    }
  },
  methods: {
    ...mapActions([
      'login'
    ]),
    onSubmit(e) {
      e.preventDefault();
      this.login({ username :  this.form.name , password: this.form.password})
        .then(() => {
          
          this.$router.push({ name: 'home' });
        })
        .catch(error => {
          console.error('Greška prilikom prijave korisnika:', error);
          
        });
    }
  }
}
</script>

  
  <style scoped>
  .naslov{
    margin-top: 6%;
    margin-right: 9%;
  }
  </style>
  