import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    partneri: [],
    destinacije: [],
    token: '',
    korisnik:''
  },
  mutations: {
    setToken(state, token) {
      state.token = token;
      localStorage.token = token;
    },
    setKorisnik(state, korisnik) { 
      state.korisnik = korisnik;
    },
    removeToken(state) {
      state.token = '';
      state.korisnik = '';
      localStorage.token = '';
    },
    addPartneri(state, partneri) {
      state.partneri = partneri;
    },
    addDestinacije(state, destinacije) {
      state.destinacije = destinacije;
    }
  },
  actions: {
    async fetchPartneri({ commit }) {
      fetch("http://localhost:9000/partneri/")
        .then(res => res.json())
        .then(data => commit('addPartneri', data));
    },
    async fetchDestinacije({ commit }) {
      fetch("http://localhost:9000/destinacije/")
        .then(res => res.json())
        .then(data => commit('addDestinacije', data));
    },
    async register({ commit }, obj) {
      const cookies = document.cookie.split('=');
      const token = cookies[cookies.length - 1]; // Uzmi token iz kolacica
      fetch('http://localhost:9001/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
      }).then(res => res.json())
        .then(data => {
          commit('setToken', data.token);
          
          localStorage.setItem('token', data.token); // Dodaj token u localStorage
        });
    },
    async login({ commit }, credentials) {
      const cookies = document.cookie.split('=');
      const token = cookies[cookies.length - 1]; // Uzmi token iz kolacica
      fetch('http://localhost:9001/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(credentials)
      }).then(res => res.json())
        .then(data => {
          if (data.msg) {
            alert(data.msg + "nije uspeo da izvadi datuu");
          } else {
            
            commit('setToken', data.token);
            console.log(credentials.username);
           commit('setKorisnik',credentials.username);
            localStorage.setItem('token', data.token); // Dodaj token u localStorage
          }
        });
    }
    
    
  }
})
