<template>
    <div id="app">
      <HeaderH class="naslov" title="Create account"/>
      <b-form @submit="onSubmit">
        <b-form-group label="Email address:" label-for="email">
          <b-form-input id="email" v-model="form.email" type="email" placeholder="Enter email" required></b-form-input>
        </b-form-group>
        <b-form-group label="User Name:" label-for="name">
          <b-form-input id="name" v-model="form.name" placeholder="Enter name" required></b-form-input>
        </b-form-group>
        <b-form-group label="Password:" label-for="password">
          <b-form-input id="password" v-model="form.password" type="password" required></b-form-input>
        </b-form-group>
        <b-button type="submit" variant="primary">Register</b-button>
      </b-form>
    </div>
  </template>
  
  <script>
  import HeaderH from '@/components/HeaderH.vue';
  import { mapActions } from 'vuex';
  
  export default {
    name: 'Register',
    components: {
      HeaderH
    },
    data() {
      return {
        form: {
          email: '',
          name: '',
          password: '',
        }
      }
    },
    methods: {
      ...mapActions([
        'register'
      ]),
      onSubmit(e) {
        e.preventDefault();
        this.register({username: this.form.name,password: this.form.password,admin:false,email: this.form.email})
        .then(() => {
          
          this.$router.push({ name: 'home' });
        })
        .catch(error => {
          console.error('Greška prilikom prijave korisnika:', error);
          
        });
      }
    }
  }
  </script>
  
  <style scoped>
  .naslov{
    margin-top: 7%;
    margin-left: 10%;
    margin-right: 10%;
  }
  #app{
    margin-left: 10%;
    margin-right: 10%;
  }
  </style>
  